

@keyframes animateSvg{
    to{
        transform: translate(-50%, -10px);
    }
}

@keyframes upanddown{
    to{
        transform: translateY(-5px);
    }
}

@keyframes upupdowndown{
    to{
        transform: translateY(-10px);
    }
}
@keyframes scrollAnimation {
    0%, 100% {
      transform: translateX(0);
    }
    50% {
      transform: translateX(calc(-50%));
    }
  }